import React, { useState, useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import { Button } from '@material-ui/core'
import Publish from './Publish';
import GridList from '@material-ui/core/GridList';

function TopBar(props) {
    const buttonList = ['再點一次', '熱賣商品', '飲品', '拼盤套餐', '煎烤土司', '漢堡', '手工蛋餅', '文青咖啡'];

    const useStyles = makeStyles((theme) => ({
        root: {
            display: 'flex',
            flexWrap: 'nowrap',
            justifyContent: 'space-between',
            overflow: 'hidden',
            backgroundColor: '#F3F3F3',
        },
        gridList: {
            flexWrap: 'nowrap',
            transform: 'translateZ(0)',
            whiteSpace: 'nowrap',
            padding: '10px'
        },
        TopBarButton: {
            background: '#FFFFFF',
            margin: '5px',
            borderRadius: '25px',
            border: 'none',
            fontSize: '15px',
        },
        TopBarSelected: {
            background: '#F0941E',
            color: '#FFFFFF',
            margin: '5px',
            borderRadius: '25px',
            border: 'none',
            fontSize: '15px',
        },
        itemCount: {
            display: 'block',
            position: 'absolute',
            right: '-9px',
            top: '0px',
            fontSize: '14px',
            textAlign: 'center',
            background: '#CB5A4B',
            padding: '0px 5px 0px 5px',
            margin: '0 auto',
            borderRadius: '50%',
            color: '#FFFFFF'
        }

    }));
    const [selectIndex, setSelectIndex] = useState(1)

    const classes = useStyles();

    function clickHandler(e) {
        setSelectIndex(e.currentTarget.getAttribute('button-index'))
    }

    useEffect(() => {
        props.setValue(selectIndex);
        props.setTitle(buttonList[selectIndex]);
    })

    function ListButton(props) {
        return (
            <Button
                className={props.index === selectIndex ? classes.TopBarSelected : classes.TopBarButton}
                text={props.title}
                button-index={props.index}
                onClick={clickHandler}>
                {props.title}
                {props.index % 2 === 1 ? <div className={classes.itemCount}>1</div> : <div></div>}
            </Button>

        )
    }

    return (
        <div className={classes.root}>
            <GridList className={classes.gridList} cellHeight={30}>
                <Publish />
                {buttonList.map((title, index) => (
                    <div>
                        <ListButton key={index} value={title} index={index} title={title} />

                        {/* <div className={classes.itemCount}>1</div> */}
                    </div>
                ))}
            </GridList>
        </div>

    )
}

export default TopBar
