import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import Button from '@material-ui/core/Button';
import Numeric from './Numeric'



function FoodCard(props) {

    const useStyles = makeStyles({
        card: {
            display: 'flex',
            alignItems: 'center'
        },
        cardDetails: {
            flex: 1,
        },
        cardMedia: {
            width: 160,
            height: 160,
            margin: '5px'
        },
        dialogMedia: {

            display: 'block',
            marginLeft: 'auto',
            marginRight: 'auto',
            marginBottom: '20px',
            width: '40vw',
            height: '40vw',
        },
        plusButton: {
            color: '#000000',
            borderRadius: '25px',
            fontSize: '14px',
            margin: '5px'
        },
        itemCount: {
            display: 'block',
            height: '25px',
            width: '25px',
            lineHeight: '25px',
            position: 'absolute',
            right: '-5px',
            top: '-5px',
            fontSize: '16px',
            textAlign: 'center',
            background: '#ffd21d',
            padding: '0px 0px',
            margin: '0 auto',
            borderRadius: '50%'
        }

    });

    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };

    const [buttonColor1, setButtonColor1] = React.useState('#FFFFFF');
    const [buttonColor2, setButtonColor2] = React.useState('#FFFFFF');


    const confirmButtobStyle = {
        background: '#F0941E',
        color: '#FFFFFF',
        padding: '0px 30px 0px 30px',
        borderRadius: '25px',
        border: 'none',
        fontSize: '18px',
    }

    const cancelButtobStyle = {
        background: '#979797',
        color: '#FFFFFF',
        padding: '0px 30px 0px 30px',
        borderRadius: '25px',
        border: 'none',
        fontSize: '18px',

    }

    const changeColor1 = () => {
        buttonColor1 === '#F0941E' ? setButtonColor1('#FFFFFF') : setButtonColor1('#F0941E')

    }

    const changeColor2 = () => {
        buttonColor2 === '#F0941E' ? setButtonColor2('#FFFFFF') : setButtonColor2('#F0941E')

    }


    const classes = useStyles();
    const { post } = props;

    return (
        <div>
            <Grid item xs={12}>
                <CardActionArea component="a" onClick={handleClickOpen}>
                    <Card className={classes.card}>
                        <div className={classes.itemCount}>1</div>

                        <div className={classes.cardDetails}>
                            <CardContent>
                                <Typography variant="h5">
                                    {post.foodName}
                                </Typography>
                                <Typography variant="subtitle1" color="textSecondary">
                                    {post.description}
                                </Typography>
                                <Typography variant="subtitle1" paragraph>
                                    ${post.price}
                                </Typography>
                            </CardContent>
                        </div>
                        <div>
                            <CardMedia className={classes.cardMedia} image='https://via.placeholder.com/150' title={post.imageTitle}>
                            </CardMedia>
                        </div>

                    </Card>
                </CardActionArea>
            </Grid>
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
                fullWidth={true}
                maxWidth={'md'}

            >
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        <CardMedia className={classes.dialogMedia} image='https://via.placeholder.com/150' title={post.imageTitle} />
                        <Typography style={{ color: 'black', fontWeight: 'bold' }} >國王厚牛美式漢堡</Typography>
                        <Typography style={{ color: 'black', fontWeight: 'bold' }}>$10000</Typography>
                        <hr></hr>
                        <Typography>非烤物</Typography>
                        <Typography style={{ color: 'black', fontWeight: 'bold', margin: '30px 0px 0px 0px' }} >加料區</Typography>
                        <Button className={classes.plusButton} variant='outlined' style={{ background: buttonColor1 }} onClick={changeColor1}>加起司+$10</Button>
                        <Button className={classes.plusButton} variant='outlined' style={{ background: buttonColor2 }} onClick={changeColor2}>加蛋+$10</Button>
                        <Numeric />

                    </DialogContentText>
                </DialogContent>
                <DialogActions style={{
                    justifyContent: 'center'
                }}>
                    <Button onClick={handleClose} color="primary" autoFocus style={cancelButtobStyle}>
                        回目錄
                </Button>
                    <Button onClick={handleClose} color="primary" autoFocus style={confirmButtobStyle}>
                        選好了
                </Button>
                </DialogActions>
            </Dialog>
        </div >

    )
}

export default FoodCard
