import React, { useState, useEffect } from 'react';
import './App.css';
import FootBar from './components/FootBar';
import TopBar from './components/TopBar';
import FoodListView from './components/FoodListView'
import SearchBar from './components/SearchBar';
import OrderAgainListView from './components/OrderAgainListView';
import ShoppingCar from './components/ShoppingCar'
import FindGroup from './components/FindGroup'



function App() {
  const [selectedIndex, setSelectIndex] = React.useState(1);
  const [title, setTitle] = useState('')
  function changeSelectedIndex(newValue) {
    setSelectIndex(newValue);
  }

  function changeTitle(newValue) {
    setTitle(newValue);
  }

  useEffect(() => {
    console.log(selectedIndex)
  })


  return (

    <div>
      <TopBar setValue={changeSelectedIndex} setTitle={changeTitle} />
      <SearchBar />
      {selectedIndex === 0 ? <OrderAgainListView /> : <FoodListView title={title} />}
      {/* <FoodListView title='熱賣商品' /> */}
      <ShoppingCar />
      <FindGroup />
      {/* <OrderAgainListView /> */}
      <FootBar />
    </div>
  );
}

export default App;
